.alerta {
    padding: 0.5rem;
    text-align: center;
    color: $blanco;
    font-weight: $bold;
    text-transform: uppercase;
    margin: 1rem 0;

    &.error {
        background-color: $rojo;
    }

    &.exito {
        background-color: $verde;
    }
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: $verde;

        th {
            color: $blanco;
            padding: 2rem;
        }
    }

    tbody {
        td {
            text-align: center;
            border-bottom: 1px solid $gris;
            border-right: 1px solid $gris;
        }
        td:last-of-type {
            border-right: none;
        }
    }

    .imagen-tabla {
        width: 10rem;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
}


.imagen-small {
    width: 25rem;
}